import React, {useState} from 'react';
import './MainBalanceCss.css';


export default function MainBalance () {
  const [balance, setBalance] = useState(502571.92);

  return <section className='balance_section'>
    <div className='balance'>
      <p className='balance_text'>
        Ваш баланс
      </p>
      <p className='balance_num'>
        {`${new Intl.NumberFormat('ru-RU').format(balance)} \u20bd`}
      </p>
    </div>
    <div className='balance_btn'>
      <button>
        <p className='balance_btn_imgFirst'></p>
        <p>Пополнить</p>
      </button>
      <button>
        <p className='balance_btn_imgSecond'></p>
        <p>Перевести</p>
      </button>
      <button>
        <p className='balance_btn_imgThird'></p>
        <p>Выставить счет</p>
      </button>
    </div>
  </section>
}