import MainHeader from "./mainHeader/MainHeader";
import MainBalance from "./MainBalance/MainBalance";
import React, { useState } from "react";
import NavMain from "./NavMain/NavMain";
import ComingBalance from "./ComingBalance/ComingBalance";

export default function MainPage () {
  const [openLastOperation, setOpenLastOperation] = useState(true);

  const changeLastOperationVisible = ()=>{
    setOpenLastOperation(!openLastOperation);
  }

  return (<>
    <main>
      <MainHeader/>
      <MainBalance/>
      <div className="last_operation">
        <p>Последние операции</p>
        <button onClick={changeLastOperationVisible}>Больше</button>
      </div>
      <ComingBalance/>
      <div className="pay">
        <p>Оплатить</p>
        <div className="payBtn">
          <button 
            className="tel"
            onClick={()=>{}}
          >
            <p></p>
            <p>Телефон</p>
          </button>
          <button 
            className="ithernet"
            onClick={()=>{}}
          >
            <p></p>
            <p>Интернет</p>
          </button>
          <button 
            onClick={()=>{}}
            className='tv'
          >
            <p></p>
            <p>Телевидение</p>
          </button>
        </div>
      </div>
      <NavMain/>
    </main>
  </>)
}