import React from "react";


export default function ComingBalance (prop){

  const text = 'Пополнение';
  const data = '28 Сентября';
  const money = 165900; 
  const time = '13:20'

  return <>
    <p>{data}</p>
    <div className="comingBalance">
      <div className="comingBalance_left">
        <p className="comingBalance_left__img"></p>
        <p className="comingBalance_left__text">
          <p>{text}</p>
          <p>{time}</p>
        </p>
      </div>
      <div className="comingBalance_right">
        <p id={money>=0?'plus':'minus'}>{`${money>=0?'+':''} ${new Intl.NumberFormat('ru-RU').format(money)} \u20bd`}</p>
      </div>
    </div>
  </>
}